@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@700&display=swap);
html,body{background-image:linear-gradient(to top, #c79081 0%, #dfa579 100%);font-family:"Bitter", serif;color:#302512;width:100%;height:100%;margin:0px;border:0;overflow:hidden;display:block}h1{margin:auto;padding:25px 25px 10px 25px;font-size:2.5vw;text-align:end}h2{font-size:2vw;text-align:center}#quote-box{background-color:rgba(253,255,255,0.473);position:absolute;min-width:65%;max-width:100%;top:50%;left:50%;transform:translate(-50%, -50%);border-radius:45px 15px 45px 15px;-moz-border-radius:45px 15px 45px 15px;-webkit-border-radius:45px 15px 45px 15px;border:0px solid #000000;box-shadow:8px 11px 24px -7px rgba(99,95,87,0.87)}.buttons{display:flex;justify-content:space-around;text-align:justify;align-items:center;color:rgba(48,37,18,0.9)}#tweet-quote{display:inline-block;color:inherit;transition:ease 0.3s;font-size:5vw}#tweet-quote:hover{cursor:pointer;color:#239bca;transform:scale(1.1, 1.1)}#new-quote{transition:ease 0.3s;font-size:10vw;transform:rotate(90deg)}#new-quote:hover{cursor:pointer;color:#302512;transform:rotate(90deg) scale(1.1, 1.1)}@media (max-width: 1024px){h1{font-size:5vw}h2{font-size:3.5vw}#quote-box{min-width:90%;max-width:100%}#tweet-quote{font-size:10vw}#new-quote{font-size:15vw}}

